  font-family: 'Roboto';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('truetype');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Oswald';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('truetype');
  font-weight: 400;
  font-style: normal;
}

:root {
  --main-bg-color: rgb(244, 250, 255);
  --secondary-bg-color: rgb(18, 178, 214);
  --card-bg-color: rgb(255, 255, 255);
  --primary-text-color: black;
  --secondary-text-color: white;
  --image-border-color: rgb(255, 255, 255);
  --border-radius: 5%;
  --card-box-shadow: 5px 5px 14px 0px #898989;
}

body {
  background-color: var(--main-bg-color);
  margin: 0;
  padding: 0;
  font-family: 'Roboto';
}

header {
  padding-top: 3rem;
  margin-bottom: 50px;
  margin-right: 1rem;
  margin-left: 1rem;
}

h1 {
  font-size: 4em;
  /* font-family: 'Oswald'; */
}

h2 {
  font-size: 2em;
}

header::before {
  content: '';
  position: absolute;
  height: 30rem;
  top: -11rem;
  right: 0;
  left: 0;
  bottom: 0;
  background-image: linear-gradient(
    to right top,
    #d16ba5,
    #c777b9,
    #ba83ca,
    #aa8fd8,
    #9a9ae1,
    #8aa7ec,
    #79b3f4,
    #69bff8,
    #52cffe,
    #41dfff,
    #46eefa,
    #5ffbf1
  );
  z-index: -1;
  transform: skewY(-4deg);
  box-shadow: 5px 5px 14px 0px #0000004e;
}

h1 {
  text-align: center;
  color: var(--main-bg-color);
  /* rotate: -2deg; */
  /* transition: ease-in-out 400ms; */
  animation: seesawName 1600ms alternate infinite; /* animation set */
  animation-play-state: paused;
}

h1:hover {
  animation-play-state: running; /* Run on hover! */
}
/* 
@keyframes rotate360 {
  to {
    transform: rotate(360deg);
  }
} */

/* transform: rotate(4deg); */
/* animation: 4000ms seesawName alternate infinite;
  animation-fill-mode: both; */

@keyframes seesawName {
  from {
    transform: rotate(-4deg);
  }
  to {
    transform: rotate(4deg);
  }
}

.headerAlignCont {
  display: flex;
  justify-content: center;
}
.headerCont {
  display: grid;
  grid-template-columns: 1fr 2fr;
  max-width: 65em;
}

@media (max-width: 600px) {
  .headerCont {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .headerImg {
    max-width: 15rem;
  }
}

.headerImg {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  transition: 400ms ease-in-out;
}

.headerImg > img {
  max-width: 100%;
  border: 10px solid var(--image-border-color);
  border-radius: var(--border-radius);
  rotate: 2deg;
  translate: 15px;
  /* transform: translateX(15px) rotate(2deg); */
  box-shadow: var(--card-box-shadow);
  /* transition: transform 1s ease-out 0.25s;
  rotate(-2deg) */
}

.headerImg:hover {
  transform: rotate(-2deg);
}

.headerCard {
  background-color: var(--card-bg-color);
  /* max-width: 350px; */
  padding: 25px;
  /* flex: 1; */
  box-shadow: var(--card-box-shadow);
  border-radius: 2%;
  display: flex;
  flex-direction: column;
}

.headerCard > h2 {
  font-size: 1.5em;
}

.headerCard > p {
  margin-bottom: 0;
  margin-top: 1rem;
}

.warning {
  color: red;
}

.iconCont {
  display: flex;
  flex-direction: row;
  gap: 4px;
  margin-top: 1rem;
}

header .iconCont {
  align-self: flex-end;
  font-size: 1.5rem;
}

.linkedInBlack {
  color: black;
}

.linkedInWhite {
  color: white;
}

a {
  text-decoration: none;
}
main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.workTitle {
  margin-bottom: 0;
}

.gridFlex {
  display: flex;
  width: 80%;
}

.projectGrid {
  flex-grow: 1;
  display: grid;
  /* grid-template-columns: repeat(auto-fill, minmax(350px, min-content)); */
  /* grid-auto-columns: fit-content(320px); */
  /* grid-template-columns: repeat(auto-fit, minmax(350px, minmax(1fr, 450px))); */
  grid-template-columns: repeat(auto-fill, minmax(320px, 0.7fr));
  justify-content: center;
  /* grid-template-columns: repeat(auto-fit, clamp(320px, 0.3fr, 400px)); */

  /* grid-template-columns: 1fr 1fr 1fr; */
  gap: 2rem;
  /* margin: auto; */
  /* width: 100%; */
  /* justify-items: center; */
  perspective: 800px;
}

.openNewImg {
  width: 1em;
}

.projectCard {
  display: flex;
  flex-direction: column;
  background-color: var(--card-bg-color);
  box-shadow: var(--card-box-shadow);
  border-radius: 2%;
  /* max-width: 90%; */
  padding: 2rem 2rem;
  box-sizing: border-box;
}

.projectCard:hover {
  box-shadow: 0px 0px 15px 3px #d16ba5;
  transform: translateZ(20px);
  /* transform: scale(103%); */
  transition: ease-in-out 400ms;

  /* translate(10px, -10px) */
}

.iconCont > *:hover {
  transform: scale(110%);
  transition: ease-in-out 40ms;
}

.projectCard > img {
  border-radius: 2%;
  aspect-ratio: 1/0.8;
  width: 100%;
}

.proCardHeader {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
  font-size: 1.5em;
}

.proCardHeader > h3 {
  margin-bottom: 0;
}

footer {
  color: var(--main-bg-color);
  display: flex;
  /* justify-content: center; */
  position: relative;
  margin-top: 8rem;
  width: 100%;
  bottom: 0;
  background-image: linear-gradient(
    to right top,
    #d16ba5,
    #c777b9,
    #ba83ca,
    #aa8fd8,
    #9a9ae1,
    #8aa7ec,
    #79b3f4,
    #69bff8,
    #52cffe,
    #41dfff,
    #46eefa,
    #5ffbf1
  );
  padding: 2rem 0rem;
  box-shadow: var(--card-box-shadow);

  /* box-shadow: rgba(17, 12, 46, 0.15) 0px -18px 100px 0px; */
  /* padding: 6rem 0rem; */
}

footer h2 {
  margin-bottom: 0;
}

footer p {
  margin-bottom: 0;
}

.contactMe {
  padding: 0rem 2rem;
}

.linkedInContactDiv {
  display: flex;
  gap: 2rem;
  font-size: 1.5rem;
  align-items: baseline;
}

footer .iconCont:hover {
  transform: rotate(360deg);
  transition: ease-in-out 400ms;
}

footer a:visited {
  text-decoration: none;
  color: white;
}
footer a:hover {
  text-decoration: none;
  color: blue;
}
footer a:focus {
  text-decoration: none;
  color: yellow;
}
footer a:hover,
footer a:active {
  text-decoration: none;
  color: black;
}

footer a {
  text-decoration: none;
}
